import test from 'ava'
const _Needs = require('./index.js')

test(t => {
    testStrictCorrect(t)
    testStrictIncorrect(t)
    t.pass()
})

function testStrictCorrect(t) {
    let needs = _Needs({})
    let test = needs.params({
        a: 'int',
        b_: 'float',
        c: {
            d_: 'int[]',
            e: {
                f: 'str',
                g: 'datetime'
            }
        }
    })
    return test({
        body: {
            a: 42,
            b: 3.14,
            c: {
                e: {
                    f: 'test',
                    g: Date.now()
                }
            }
        }
    }, null, (err)=>{
        if (err) {
            console.log('testStrictCorrect', err)
            t.fail()
        }
    })
}
function testStrictIncorrect(t) {
    let needs = _Needs({ onError: options => { return 'Successful Error Test' } })
    let test = needs.params({
        a: 'int',
        b_: 'float',
        c: {
            d_: 'int[]',
            e: {
                f: 'str',
                g: 'datetime'
            }
        }
    })
    return test({
        body: {
            a: 42,
            b: 3.14,
            c: {
                e: {
                    f: 'test',
                    g: Date.now()
                },
                f: 1
            }
        }
    }, null, (err)=>{
        if (!err || err.expected) {
            console.log('testStrictIncorrect', err)
            t.fail()
        }
    })
}